.footer-address {
  display: flex;
  flex-direction: column;
  font-style: normal;

  @include mq(tablet, max) {
    margin: 20px 0 0;
    text-align: center;
  }

  p, a {
    @include fz(16px);
    margin: 4px 0;
    color: rgba(255, 255, 255, .5);
    font-family: $f-muller-medium;
    letter-spacing: .3px;
    text-decoration: none;
  }
}