.footer__logo {
  @include fz(12px);
  margin: 0 auto;
  max-width: 160px;
  color: rgba(255, 255, 255, .5);
  font-family: $f-muller-medium;
  letter-spacing: .3px;
  text-decoration: none;

  @include mq(tablet) {
    margin: 0;
  }

  @include mq(desktop) {
    max-width: 100%;
  }
}