.nav-phone {
  position: relative;
  display: flex;
  font-family: $f-muller-medium;
  letter-spacing: .3px;

  @include mq(desktop, max) {
    align-items: center;
    justify-content: center;
    padding: 40px 0;
    margin:  100px 0 0;
    border-top: 1px solid rgba(255,255,255, .2);
    border-bottom: 1px solid rgba(255,255,255, .2);
  }

  @include mq(tablet, max) {
    justify-content: flex-start;
    margin:  40px 0 0;
    padding: 40px 20px;
  }

  @include mq(desktop) {
    flex-direction: column;
    padding: 0 30px;

    &::before, &::after {
      content: "";
      position: absolute;
      top: 50%;
      width: 1px;
      height: 30px;
      background-color: $c-border;
      transform: translateY(-50%);
    }

    &::before {
      left: 0;
    }

    &::after {
      right: 0;
    }
  }

  @media screen and (min-width: 1440px) {
    padding: 0 40px;
  }

  @media screen and (min-width: 1900px) {
    padding: 0 60px;
  }
}
