.nav-compare {
  position: relative;
  display: inline-block;

  &_adaptive {
    @include mq(desktop, max) {
      display: none;
    }
  }

  &__link {
    display: block;
    text-decoration: none;
  }

  &__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 28px;
    height: 28px;
    fill: $c-blue;
  }

  &__count {
    position: absolute;
    right: -3px;
    bottom: -6px;
    width: 14px;
    height: 14px;
    font-size: 10px;
    font-family: $f-muller-medium;
    color: $c-white;
    line-height: 16px;
    text-align: center;
    background-color: #CE001E;
    border-radius: 50%;
  }
}
