$c-blue: #08396f;
$c-dark: #222222;
$c-white: #ffffff;
$c-light-blue: #89bdff;
$c-dark-blue: #3c589e;
$c-blue-shadow: rgba(148, 183, 228, .55);
$c-main-bg: #fcfcfc;
$c-red: #b10012;
$c-border: #bbbbbb;
$c-link-default: #053a6f;
$c-link-hover: #8daede;
$c-link-active: #8daede;
$c-field-default: #B6B6B6;
$retina-dppx: 1.5dppx;

//  2. Breakpoints
//  --------------

$breakpoints: (
  "tablet": 768px,
  "desktop": 1366px
);

