.footer {
  padding: 20px;
  background-color: $c-blue;

  @include mq(tablet) {
    padding: 20px 20px 60px;
  }

  @include mq(desktop) {
    flex-direction: row;
    padding: 54px 2% 60px;
  }

  &__wrap {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding:
      env(safe-area-inset-top)
      env(safe-area-inset-right)
      env(safe-area-inset-bottom)
      env(safe-area-inset-left);

    @include mq(desktop) {
      flex-direction: row;
    }
  }
}

