.footer__col_tablet {
  display: flex;

  @media screen and (min-width: 768px) and (max-width: 1365px) {
    align-items: flex-end;
    justify-content: space-between;
  }

  @include mq(desktop) {
    flex-direction: column;
    justify-content: space-between;
    max-width: 310px;
    border-right: none;
  }
}