.footer-form {
  position: relative;
  margin: 30px 0 0;

  @include mq(desktop) {
    width: 100%;
    margin: 28px 0 0;
  }

  .button {
    @include mq(desktop, max) {
      position: absolute;
      right: 0;
      bottom: 0;
      height: 100%;

      &::before {
        border-radius: 0 25px 25px 0;
      }
    }
  }
}