.nav-dropdown {
  position: relative;

  @include mq(tablet) {
    height: 46px;
  }

  &_adaptive {
    padding:  40px 20px;

    @include mq(tablet) {
      display: none;
    }

    @include mq(desktop) {
      display: block;
      padding: 0;
    }
  }

  &__link {
    position: relative;
    display: flex;
    align-items: center;
    height: 100%;
    padding: 6px 0;
    @include fz(16px);
    color: $c-blue;
    font-family: $f-muller-medium;
    letter-spacing: .3px;
    text-decoration: none;
    text-transform: uppercase;

    @include mq(tablet) {
      flex-direction: column;
      justify-content: space-between;
    }

    span {
      @include mq(tablet, max) {
        margin: 0 10px 0 0;
      }
    }
  }

  &__icon {
    stroke: #0d376f;
    fill: $c-blue;
  }

  .summary-list {
    position: relative;
    padding: 30px;
    margin: 0;
    background-color: $c-white;
    border-radius: 10px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, .07);
    list-style: none;

    &:before {
      content: "";
      position: absolute;
      top: -10px;
      right: 10px;
      border-style: solid;
      border-width: 0 8px 14px 8px;
      border-color: transparent transparent $c-white transparent;
    }

    @include mq(tablet, max) {
      display: flex;
      justify-content: space-between;
      padding: 20px;

      &:before {
        display: none;
      }
    }

    &__item {
      margin: 2px 0;
    }

    &__link {
      @include fz(16px);
      color: $c-field-default;
      font-family: $f-muller-medium;
      letter-spacing: .3px;
      text-decoration: none;
      text-transform: uppercase;

      &:hover {
        color: $c-blue;
      }
    }

    &-wrap {
      position: absolute;
      top: 4px;
      right: 0;
      display: none;
      padding: 20px 0 0;

      @include mq(tablet, max) {
        width: calc(100% - 120px);
        right: 20px;
      }

      @include mq(tablet) {
        top: 100%;
      }
    }
  }

  &:hover .summary-list-wrap {
    display: block;
  }

  &:focus-within .summary-list-wrap {
    display: block;
  }
}
