html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

html, body {
  height: 100%;
  font-family: $f-din-bold;
}

body {
  padding: 0;
  margin: 0;
  line-height: 1.25;
  background-color: $c-main-bg;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.container {
  width: 100%;
  padding: 0 20px;

  @include mq(tablet) {
    max-width: 768px;
    margin: 0 auto;
  }

  @include mq(desktop) {
    max-width: 1300px;
    padding: 0;
  }
}

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.header, .footer {
  flex: 0 0 auto;
}

.main {
  flex: 1 0 auto;
  margin: 46px 0 0;

  @include mq(desktop) {
    margin: 0;
  }
}

.center {
  margin: auto;
}

.m-hidden {
  @include mq(tablet, max) {
    display: none;
  }
}

.d-hidden {
  @include mq(tablet) {
    display: none;
  }
}

.white-logo {
  display: none;
}

.no-js,
.isOpen {
  .adaptive-list__item_btn-open {
    display: none;
  }

  .adaptive-list__item_btn-close {
    display: block;
  }

  .blue-logo {
    display: none;
  }

  .white-logo {
    display: block;
    margin: auto;

    @include mq(tablet) {
      transform: translateX(50%);
    }
  }

  @include mq(desktop, max) {
    & .header {
      background-color: $c-blue;
    }

    & .nav-list__link,
    & .nav-dropdown__link {
      color: $c-white;
    }

    & .nav-dropdown__icon {
      stroke: $c-white;
    }

    & .nav-dropdown__icon,
    & .nav-compare__icon {
      fill: $c-white;
    }

    & .nav__inner {
      display: block;

      @include mq(desktop) {
        display: flex;
      }
    }
  }
}