.nav-list {
  display: flex;
  align-items: center;
  padding: 0;
  margin: 0;
  list-style: none;

  @include mq(desktop, max) {
    padding: 0 20px;
    flex-direction: column;
  }

  @include mq(tablet, max) {
    align-items: flex-start;
  }

  @include mq(desktop) {
    margin: 0 15px 0 0;
  }

  @media screen and (min-width: 1440px) {
    margin: 0 20px 0 0;
  }

  @media screen and (min-width: 1900px) {
    margin: 0 30px 0 0;
  }
}
