.footer-form__icon {
  position: absolute;
  top: 50%;
  left: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  transform: translateY(-50%);

  @include mq(desktop) {
    top: 25px;
  }

  svg {
    width: 100%;
    height: 100%;
  }
}