.header {
  padding: 20px 0 0;
  background-color: $c-main-bg;

  @include mq(tablet) {
    padding: 30px 0 0;
  }

  @include mq(desktop) {
    padding: 40px 2rem;
    margin: 0;
    border-bottom: none;
  }

  &__wrap {
    padding:
      env(safe-area-inset-top)
      env(safe-area-inset-right)
      env(safe-area-inset-bottom)
      env(safe-area-inset-left);
  }

  &__top {
    margin: 0 20px;

    @include mq(desktop) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin: 0;
    }
  }

  &__logo {
    display: none;

    @include mq(desktop) {
      display: block;
    }

    &_adaptive {
      display: block;

      @include mq(tablet, max) {
        width: 30px;
        height: 60px;
        overflow: hidden;

        img {
          max-width: none;
          height: 100%;
        }
      }

      @include mq(tablet) {
        height: 60px;

        img {
          height: 100%;
        }
      }
    }
  }
}

.close-btn, .open-btn {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0;
  margin: 0;
  background: transparent;
  border: none;

  svg {
    width: 100%;
    height: 100%;
  }
}

.open-btn {
  width: 36px;
  height: 36px;
}

.close-btn {
  height: 24px;
  width: 32px;
}

.adaptive-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 0 0 20px;
  margin: 0;
  border-bottom: 1px solid rgba(255, 255, 255, .2);

  @include mq(tablet) {
    padding: 0 0 30px;
  }

  @include mq(desktop) {
    display: none;
  }

  &__item {
    list-style-type: none;

    &_btn-close {
      display: none;
    }

    &_logo {
      @include mq(tablet, max) {
        order: 1;
      }

      @include mq(tablet) {
        flex-grow: 1;
      }
    }

    &_btn-open {
      @include mq(tablet, max) {
        order: 0;
      }
    }

    &_compare {
      @include mq(tablet, max) {
        order: 2;
      }

      @include mq(tablet) {
        margin: 0 60px;
      }
    }

    &_dropdown {
      @include mq(tablet, max) {
        display: none;
      }
    }
  }
}
